import React from 'react'
import "./Education.css";
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from 'react-responsive-carousel';
import { books } from '../../portfolio';
function Books() {
  return (
    <section className='section skills' id='books'>
    <h4 className='section__title'>Books</h4>
      <p className='about__desc2'>I am also the author of the following books</p>
      <br/>
      <div style={{width: 350, margin: "auto"}}>
      <Carousel 
      width={350}
      autoPlay
      showIndicators={false}
      infiniteLoop
      interval={5000}
      showArrows={true}>
        {
          books.map((a)=> (
            <div>
            <img style={{maxWidth: 300}} src={a} />
        </div>
          ))
        }
               
            </Carousel>
      </div>
  </section>
  )
}

export default Books