import React from "react";
import Techs from "../Skills/Techs";
import Books from "./Books";
import "./Education.css";

export const Education = ({ }) => {
  return (
    <div style={{ alignItems: "center" }}>
      <section id='education' className='section education'>

        <h3 className='section__title'>Education</h3>
        <ul class="events">
          <li>
            <time><span style={{ fontSize: 10 }}>Class of </span>2017</time>
            <span className="spanF"><strong>School</strong>Roshani English Board. School</span><br /></li>

          <li>
            <time><span style={{ fontSize: 10 }}>Class of </span>2019</time>
            <span className="spanF"><strong>High School</strong>Kathmandu Model College<br /><span style={{ color: "#999", marginTop: "9px" }}>Science(Physical)</span></span></li>

          <li>
            <time>2019-</time>
            <span className="spanF"><strong>College</strong>Pulchowk Engineering College<br /><span style={{ color: "#999", marginTop: "9px" }}>Civil Engineering</span></span></li>
        </ul>
      </section>
      <Techs />
      <Books />
    </div>
  );
}