import { header } from '../../portfolio'
import Navbar from '../Navbar/Navbar'
import './Header.css'

const Header = () => {
  const { homepage, title, myImage } = header

  return (
    <header className='header center'>
      <h3>
        {homepage ? (
          <a href={homepage} style={{display: "flex", alignItems: "center"}} className='link'>
           <div  className="image-me">
            <img src={myImage} alt="me" style={{width: 40, height: 40, borderRadius: 100, marginRight: 10, position: "relative"}}/>
           </div>
            {title}
          </a>
        ) : (
          title
        )}
      </h3>
      <Navbar />
    </header>
  )
}

export default Header
