import uniqid from 'uniqid'
import { skills2 } from '../../portfolio'
import './Skills.css'

const Techs = () => {
  if (!skills2.length) return null
  return (
    <section className='section skills' id='tech'>
      <h4 className='section__title'>Technologies</h4>
      <p className='about__desc2'>I have hands-on experience in following civil engineering related softwares and techs:</p>
      <br/>
      <ul className='skills__list'>
        {skills2.map((skill) => (
          <li key={uniqid()} className='skills__list-item btn btn--plain'>
            {skill}
          </li>
        ))}
      </ul>
    </section>
  )
}

export default Techs
